code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.cointenerFooter {
    background: #EDEDED;
    padding: 20px;
    bottom: 0;
    width: auto;
    height: 100px;
    text-align: center;
    margin-top: 90px;
}
.footerTxt{
    font-family: "Roboto Slab";
    font-size: 14px;
    color: #95006d;
}
.cointenerLogIn{
    font-family: 'Roboto Slab';
    text-align: center;
}
.imgLogin{
    width: 25%;
    margin: 0 auto;
    padding-top: 60px;
}
.inputTxt{
    padding: 25px;
    width: 30%;
}
.buttonLoging{
    margin-bottom: 50px;
    background: #C4D600;
    padding: 15px;
    font-size: 20px;
    margin-top: 40px;
    color: #95006D;
    font-weight: 400;
}

.cointenerHeader {
  background: #c4d600;
  display: grid;
  grid-template-columns: repeat(2, 50%);
  grid-gap: 0px;
  justify-content: center;
}
.appName {
  font-family: "Roboto Slab";
  font-size: 30px;
  color: #95006d;
  margin-left: 40px;
}
.fa-times {
  color: #95006d;
  margin: 30px 30px 0 0;
}
.buttonLogOut {
  display: grid;
  justify-items: end;
}

